import type { EventName } from "@lit-labs/react";
import * as WebComponentModule from "@vaadin/menu-bar/vaadin-menu-bar.js";
import * as React from "react";
import { createComponent, WebComponentProps } from "../utils/createComponent.js";
export type MenuBarEventMap = Readonly<{
    onItemSelected: EventName<WebComponentModule.MenuBarEventMap["item-selected"]>;
}>;
const events = { onItemSelected: "item-selected" } as MenuBarEventMap;
export type MenuBarProps = WebComponentProps<WebComponentModule.MenuBar, MenuBarEventMap>;
export const MenuBar = createComponent({
    elementClass: WebComponentModule.MenuBar,
    events,
    react: React,
    tagName: "vaadin-menu-bar"
});
export { WebComponentModule };
